import React, { useState } from "react";
import Hairshag from "./Hairshag";
import "./stile.css";
const SpeedClick = () => {
  const [onoo, setonoo] = useState(0);
  const [second, setsecond] = useState(0);

  const startGame = () => { 
    const timerId = setInterval(() => {
      setsecond((second) => {
        console.log("second", second + 10, second + 10 === 2000);
        // Game over
        if (second + 10 === 2000) {
          clearInterval(timerId);

          setTimeout(() => {
            setsecond(0);
            setonoo(0)
    
          }, 1000);
          
        }

        return second + 10;
      });
    }, 10);
   }
  const handelClick = () => {
    console.log("isPlaying", isPlaying, second);

    if (!isPlaying) {
      if (onoo === 0) {
        startGame()
      }
      return;
    } else {
      setonoo(onoo + 1);
    }
  };

  const isPlaying = second > 0;
  const clickSec = ((onoo / second) * 1000).toFixed(2);
  return (
    <div className="main">
      <div className="container">
       
        <div className="aaa">
          <Hairshag
            too={isNaN(second) ? "0" : second / 1000}
            tailbar={"timir"}
            color={"pink"}
          />
          <Hairshag
            too={isNaN(clickSec) ? "0" : clickSec}
            tailbar={"clickSec"}
            color={"orange"}
          />
          <Hairshag too={onoo} tailbar={"score"} color={"green"} />
        </div>
        <button className="tom-hairtsag" onClick={handelClick}>
          end dar
        </button>
      </div>
    </div>
  );
};
export default SpeedClick;
